import {connect} from "react-redux";

const BalanceSheet = (props) => {
    return(
        <div className="balance-sheet">
            <h2>Summary</h2>
            <div>
                <div>
                    <div>Beginning Balance</div>
                    <div>P {props.begBalance.toLocaleString('en', {useGrouping:true})}</div>
                </div>
                <div>
                    <div>Total Revenue</div>
                    <div>{props.totalRevenue.toLocaleString('en', {useGrouping:true})}</div>
                </div>
                <div>
                    <div>Total Expenses</div>
                    <div>{props.totalExpenses.toLocaleString('en', {useGrouping:true})}</div>
                </div>
                <div>
                    <div>Net Income</div>
                    <div>{props.savings.toLocaleString('en', {useGrouping:true})}</div>
                </div>
                <div>
                    <div>Ending Balance</div>
                    <div>P {props.endBalance.toLocaleString('en', {useGrouping:true})}</div>
                </div>
            </div>
            <div>
                <button onClick={props.moveToNewPeriod}>Move to new period</button>
            </div>
            <div>
                Icons made by <a href="https://www.freepik.com" title="Freepik"> Freepik </a> from <a href="https://www.flaticon.com/" title="Flaticon"> www.flaticon.com</a>
            </div>
        </div>
    );
};

const mapStateToProps = (state) => {
    return {
        begBalance: state.begBalance,
        totalRevenue: state.totalRevenue,
        totalExpenses: state.totalExpenses,
        endBalance: state.endBalance,
        savings: state.savings
    }
}

const mapDispatchToProps = dispatch => {
    return {
        moveToNewPeriod: () => dispatch({type: "MOVE_TO_NEW_PERIOD"})
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(BalanceSheet)