import IncomeStatementItem from "./IncomeStatementItem";
import {connect} from "react-redux";

const IncomeStatement = (props) => {
    return(
        <div className="income-statement">
            <div>
                <h3>{props.itemType}</h3>
                {
                    props.itemType === "revenue" ?
                    <div>P {props.totalRevenue.toLocaleString('en', {useGrouping:true})}</div>
                    :
                    <div>P {props.totalExpenses.toLocaleString('en', {useGrouping:true})}</div>
                }
            </div>                
            <div className="income-statement-itemsbox">
            {
                props.statementItems.filter( item => item.itemType === props.itemType)
                .map(statementItem =>
                    <IncomeStatementItem
                        key = {statementItem.name}
                        statementItem = {statementItem}
                    />
                )
            }
            </div>
        </div>
    );
};

const mapStateToProps = (state) => {
    return {
        totalRevenue: state.totalRevenue,
        totalExpenses: state.totalExpenses,
        statementItems: state.statementItems
    }
}

export default connect(mapStateToProps)(IncomeStatement);