import MonthlySalary from "../components/assets/imgs/monthly-salary.png";
import FreelanceIncome from "../components/assets/imgs/freelancing-income.png";
import InterestIncome from "../components/assets/imgs/interest-income.png";
import Food from "../components/assets/imgs/food.png";
import Clothing from "../components/assets/imgs/clothing.png";
import Utilities from "../components/assets/imgs/utilities.png";
import Rent from "../components/assets/imgs/rent.png";
import Health from "../components/assets/imgs/health.png";
import Entertainment from "../components/assets/imgs/entertainment.png";

const initialState = {
    begBalance: 0,
    totalRevenue: 0,
    totalExpenses: 0,
    endBalance: 0,
    savings: 0,
    statementItems: [
        {
            name: "Monthly Salary",
            img: MonthlySalary,
            itemType: "revenue",
            balance: 0
        },
        {
            name: "Freelance Income",
            img: FreelanceIncome,
            itemType: "revenue",
            balance: 0
        },
        {
            name: "Interest Income",
            img: InterestIncome,
            itemType: "revenue",
            balance: 0
        },
        {
            name: "Food",
            img: Food,
            itemType: "expenses",
            balance: 0
        },
        {
            name: "Health",
            img: Health,
            itemType: "expenses",
            balance: 0
        },
        {
            name: "Utilities",
            img: Utilities,
            itemType: "expenses",
            balance: 0
        },
        {
            name: "Rent",
            img: Rent,
            itemType: "expenses",
            balance: 0
        },
        {
            name: "Clothing",
            img: Clothing,
            itemType: "expenses",
            balance: 0
        },
        {
            name: "Entertainment",
            img: Entertainment,
            itemType: "expenses",
            balance: 0
        },
    ]
}

const reducer = (state = initialState, action) => {
    let statementItemsCopy = [...state.statementItems];
    switch (action.type) {
        case "MOVE_TO_NEW_PERIOD":
            statementItemsCopy.forEach( statementItem => statementItem.balance = 0);
            return {
                ...state,
                begBalance: state.endBalance,
                totalRevenue: 0,
                totalExpenses: 0,
                savings: 0,
                statementItems: statementItemsCopy 
            }
        case "GET_TYPE_BALANCE":
            let index = statementItemsCopy.findIndex( item => item.name === action.payload.name);
            statementItemsCopy[index].balance += action.payload.amt
            if (action.payload.itemType === "revenue") {
                return {
                    ...state,
                    totalRevenue: state.totalRevenue + action.payload.amt,
                    savings: state.savings + action.payload.amt,
                    endBalance: state.endBalance + action.payload.amt,
                    statementItems: statementItemsCopy
                }
            }
            else {
                return {
                    ...state,
                    totalExpenses: state.totalExpenses + action.payload.amt,
                    savings: state.savings - action.payload.amt,
                    endBalance: state.endBalance - action.payload.amt,
                    statementItems: statementItemsCopy
                }
            };
        default: return state
    }
}

export default reducer;