import { useState } from "react";
import plus from "./assets/imgs/plus.png";
import {connect} from "react-redux";

const AddAmountForm = (props) => {
    let [amount, setAmount] = useState(0);
    const inputAmtHandler = (e) => {
        setAmount(e.target.value * 1);
    };
    const addBtnHandler = () => {
        props.getTypeBalance(props.name, amount, props.itemType);
        setAmount(0);
        props.displayAddAmt();
    }
    return(
        <div>
            {
                props.displayAddAmtForm === true ?
                <div className="add-amt-form">
                    <p>{props.name}</p>
                    <input
                        type = "number"
                        min = {0}
                        onChange = {inputAmtHandler}
                        value = {amount}
                    />
                    <button onClick = {addBtnHandler}>
                        <img src={plus} alt="add" />
                    </button>
                </div>
                :
                null
            }
        </div>
    );
};

const mapDispatchToProps = dispatch => {
    return {
        getTypeBalance: (name, amt, itemType) => dispatch({
            type: "GET_TYPE_BALANCE",
            payload: {
                name,
                amt,
                itemType
            }
        })
    }
}

export default connect(null, mapDispatchToProps)(AddAmountForm);