import { useState } from "react";
import AddAmountForm from "./AddAmountForm";
import remove from "./assets/imgs/remove.png";

const IncomeStatementItem = (props) => {
    let [displayAddAmtForm, setdisplayAddAmtForm] = useState(false);
    const displayAddAmt = () => {
        setdisplayAddAmtForm(!displayAddAmtForm);
    };
    let {name, itemType, img, balance} = props.statementItem;
    return(
        <div className="income-statement-item">
            {
                displayAddAmtForm === true ?
                <button onClick={displayAddAmt} className="rmv-btn">
                    <img src={remove} alt={name}/>
                </button>
                :
                <div>
                    <button onClick={displayAddAmt} className={`itm-btn `+ itemType}>
                        <img
                            alt={name}
                            src={img}
                        />
                    </button>
                    <div>{name}</div>
                    <div>P {
                    balance.toLocaleString('en', {useGrouping:true})
                    }</div>
                </div>
            }
            <div>
                <AddAmountForm
                    name = {name}
                    itemType = {itemType}
                    displayAddAmtForm = {displayAddAmtForm}
                    displayAddAmt = {displayAddAmt}
                />
            </div>
        </div>
    );
};

export default IncomeStatementItem;