import "./assets/FinancialsTracker.css"
import BalanceSheet from "./BalanceSheet";
import IncomeStatement from "./IncomeStatement";

const FinancialsTracker = () => {

    return(
        <div className="main-box">
            <div className="heading">
                <h1>Cashflow Tracker</h1>
                <p>Save some. Spend some.</p>
            </div>
            <BalanceSheet />
            <div className="income-statement-mainbox">
                <IncomeStatement
                    itemType = "revenue"
                />
                <IncomeStatement
                    itemType = "expenses"
                />
            </div>
        </div>
    );
};

export default FinancialsTracker